<template>
	<div>
		<h3>Manage Slots - {{ warehouse.name }}</h3>
		<b-alert variant="success" show>
			Create and modify slots
		</b-alert>
		<b-row>
			<b-col class="text-right">
				<b-button variant="success" @click="add">
					<b-icon icon="plus" scale="1.5"></b-icon> Add
				</b-button>
			</b-col>
		</b-row>
		<b-table
			:fields='fields'
			:items='list'
			:busy="loading"
			outlined
			class="mt-3"
			responsive>
			<template #cell(index)="data">
				<p>{{ (searchForm.page - 1) * searchForm.perPage + data.index + 1 }}</p>
			</template>
			<template #cell(fromDate)="data">
				<p>{{ moment(data.value).format('DD.MM.YYYY') }}</p>
			</template>
			<template #cell(toDate)="data">
				<p>{{ moment(data.value).format('DD.MM.YYYY') }}</p>
			</template>
			<template #cell(fromTime)="data">
				{{ data.item.fromTime.hour | hourMinute }} : {{ data.item.fromTime.minute | hourMinute }}
			</template>
			<template #cell(toTime)="data">
				{{ data.item.toTime.hour | hourMinute }} : {{ data.item.toTime.minute | hourMinute }}
			</template>
			<template #cell(loadUnit)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(capacity)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(edit)="data">
				<b-button variant="success" size="sm" @click="edit(data.index)" class="mr-2">
					<b-icon icon="pencil"></b-icon> Edit
				</b-button>
				<b-button variant="danger" size="sm" @click="deleteById(data.index)">
					<b-icon icon="trash"></b-icon> Delete
				</b-button>
			</template>
		</b-table>
		<b-pagination
			v-model="searchForm.page"
			:total-rows="total"
			:per-page="searchForm.perPage"
			align="right">
		</b-pagination>

		<b-modal 
			v-model="addModalShow"
			title="Add Slot"
			hide-header-close 
			@ok="doAdd">
			<b-row>
				<b-col md="3">From Date</b-col>
				<b-col md="9">
					<b-input-group class="mb-3">
						<b-form-input
							v-model="slot.fromDate"
							type="text"
							placeholder="YYYY-MM-DD"
							size="sm"
							autocomplete="off"
							:state="!$v.slot.fromDate.required ? false : null"/>
						<b-input-group-append>
							<b-form-datepicker
								v-model="slot.fromDate"
								button-only
								right
								locale="en-US"
								size="sm"
							></b-form-datepicker>
						</b-input-group-append>
					</b-input-group>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">To Date</b-col>
				<b-col md="9">
					<b-input-group class="mb-3">
						<b-form-input
							v-model="slot.toDate"
							type="text"
							placeholder="YYYY-MM-DD"
							size="sm"
							autocomplete="off"
							:state="!$v.slot.toDate.required ? false : null"/>
						<b-input-group-append>
							<b-form-datepicker
								v-model="slot.toDate"
								button-only
								right
								locale="en-US"
								size="sm"
							></b-form-datepicker>
						</b-input-group-append>
					</b-input-group>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">From Time</b-col>
				<b-col md="4">
					<b-form-input
						v-model="slot.fromTime.hour"
						type="number"
						placeholder="Hour"
						size="sm"/>
				</b-col>
				<b-col md="1">:</b-col>
				<b-col md="4">
					<b-form-input
						v-model="slot.fromTime.minute"
						type="number"
						placeholder="Minute"
						size="sm"/>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">To Time</b-col>
				<b-col md="4">
					<b-form-input
						v-model="slot.toTime.hour"
						type="number"
						placeholder="Hour"
						size="sm"/>
				</b-col>
				<b-col md="1">:</b-col>
				<b-col md="4">
						<b-form-input
						v-model="slot.toTime.minute"
						type="number"
						placeholder="Minute"
						size="sm"/>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">Load Unit</b-col>
				<b-col md="9">
					<b-form-select v-model="slot.loadUnit" size="sm">
						<b-form-select-option value="pallet">Pallet</b-form-select-option>
						<b-form-select-option value="container">Container</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">Capacity</b-col>
				<b-col md="9">
					<b-form-input
						v-model="slot.capacity"
						type="number"
						size="sm"/>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="editModalShow"
			title="Edit Slot"
			hide-header-close 
			@ok="doEdit">
			<b-row>
				<b-col md="3">From Date</b-col>
				<b-col md="9">
					<b-input-group class="mb-3">
						<b-form-input
							v-model="slot.fromDate"
							type="text"
							placeholder="YYYY-MM-DD"
							size="sm"
							autocomplete="off"
							:state="!$v.slot.fromDate.required ? false : null"/>
						<b-input-group-append>
							<b-form-datepicker
								v-model="slot.fromDate"
								button-only
								right
								locale="en-US"
								size="sm"
							></b-form-datepicker>
						</b-input-group-append>
					</b-input-group>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">To Date</b-col>
				<b-col md="9">
					<b-input-group class="mb-3">
						<b-form-input
							v-model="slot.toDate"
							type="text"
							placeholder="YYYY-MM-DD"
							size="sm"
							autocomplete="off"
							:state="!$v.slot.toDate.required ? false : null"/>
						<b-input-group-append>
							<b-form-datepicker
								v-model="slot.toDate"
								button-only
								right
								locale="en-US"
								size="sm"
							></b-form-datepicker>
						</b-input-group-append>
					</b-input-group>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">From Time</b-col>
				<b-col md="4">
					<b-form-input
						v-model="slot.fromTime.hour"
						type="number"
						placeholder="Hour"
						size="sm"/>
				</b-col>
				<b-col md="1">:</b-col>
				<b-col md="4">
					<b-form-input
						v-model="slot.fromTime.minute"
						type="number"
						placeholder="Minute"
						size="sm"/>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">To Time</b-col>
				<b-col md="4">
					<b-form-input
						v-model="slot.toTime.hour"
						type="number"
						placeholder="Hour"
						size="sm"/>
				</b-col>
				<b-col md="1">:</b-col>
				<b-col md="4">
						<b-form-input
						v-model="slot.toTime.minute"
						type="number"
						placeholder="Minute"
						size="sm"/>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">Load Unit</b-col>
				<b-col md="9">
					<b-form-select v-model="slot.loadUnit" size="sm">
						<b-form-select-option value="pallet">Pallet</b-form-select-option>
						<b-form-select-option value="container">Container</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="3">Capacity</b-col>
				<b-col md="9">
					<b-form-input
						v-model="slot.capacity"
						type="number"
						size="sm"/>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="deleteModalShow"
			title="Delete Country"
			hide-header-close 
			@ok="doDelete"
		>
			Are you sure you want to delete?
		</b-modal>
	</div>
</template>
<script>
	import { mapState } from 'vuex'
	import moment from 'moment'
	import { required } from 'vuelidate/lib/validators'
	export default {
		name: 'SlotView',
		mounted() {
			this.fetch()
		},
		data() {
			return {
				fields: [
					{ key: 'index', label: 'No' },
					{ key: 'fromDate', label: 'From Date' },
					{ key: 'toDate', label: 'To Date' },
					{ key: 'fromTime', label: 'From Time' },
					{ key: 'toTime', label: 'To Time' },
					{ key: 'loadUnit', label: 'Load Unit' },
					{ key: 'capacity', label: 'Capacity' },
					{ key: 'edit', label: '-'}
				],
				loading: false,
				searchForm: {
					page: 1,
					perPage: 10,
					name: '',
					warehouse: this.$route.params.warehouseId
				},
				addModalShow: false,
				editModalShow: false,
				deleteModalShow: false,
				slot: {
					_id: '',
					warehouse: '',
					fromDate: '',
					toDate: '',
					fromTime: { hour: 0, minute: 0 },
					toTime: { hour: 0, minute: 0 },
					loadUnit: 'pallet',
					capacity: 0
				}
			}
		},
		validations: {
			slot: {
				fromDate: { required },
				toDate: { required }
			}
		},
		computed: {
			...mapState({
				list: state => state.slot.list,
				total: state => state.slot.total,
				warehouse: state => state.slot.warehouse
			}) 
		},
		methods: {
			moment,
			fetch() {
				this.loading = true
				this.$store.dispatch('slot/fetch', this.searchForm).then(() => {
					this.loading = false
				})
			},
			add() {
				this.slot = {
					_id: '',
					warehouse: this.warehouse,
					fromDate: '',
					toDate: '',
					fromTime: { hour: 0, minute: 0 },
					toTime: { hour: 0, minute: 0 },
					loadUnit: 'pallet',
					capacity: 0
				}
				this.addModalShow = true
			},
			doAdd(evt) {
				evt.preventDefault()

				this.$v.slot.$touch()
				if (this.$v.slot.$error) return

				this.addModalShow = false
				this.$store.dispatch('slot/add', this.slot).then(() => {
					this.$toasted.success('Add Success')
					this.fetch()
				})
			}, 
			edit(idx) {
				this.slot = {
					_id: this.list[idx]._id,
					warehouse: this.list[idx].warehouse,
					fromDate: this.list[idx].fromDate.substring(0, 10),
					toDate: this.list[idx].toDate.substring(0, 10),
					fromTime: this.list[idx].fromTime,
					toTime: this.list[idx].toTime,
					loadUnit: this.list[idx].loadUnit,
					capacity: this.list[idx].capacity,
				}

				this.editModalShow = true
			},
			doEdit(evt) {
				evt.preventDefault()

				this.$v.slot.$touch()
				if (this.$v.slot.$error) return

				this.editModalShow = false
				this.$store.dispatch('slot/edit', this.slot).then(() => {
					this.$toasted.success('Edit Success')
					this.fetch()
				})
			},
			deleteById(idx) {
				this.slot._id = this.list[idx]._id
				this.deleteModalShow = true
			},
			doDelete() {
				this.$store.dispatch('slot/deleteById', this.slot._id).then(() => {
					this.$toasted.success('Delete Success')
					this.fetch()
				})
			}
		},
		watch: {
			'searchForm.page': function() {
				this.fetch()
			}
		}
	}
</script>